		},
		created: function () {
			let tab = this.$route.params.tab;
			if (tab) {
				this.tab = tab;
			}
			this.loadList();
		},
		methods: {
			pageChange: function (val) {
				let _this = this;
				if (_this.tab == 'in') {//出库申请切换页码
					if (_this.inTab.searchParams.currentPage != val) {
						_this.inTab.searchParams.currentPage = val;
						_this.loadList();
					}
				} else if (_this.tab == 'out') {//库存中
					if (_this.outTab.searchParams.currentPage != val) {
						_this.outTab.searchParams.currentPage = val;
						_this.loadList();
					}
				} else if (_this.tab == 'history') {//历史记录
					if (_this.historyTab.searchParams.currentPage != val) {
						_this.historyTab.searchParams.currentPage = val;
						_this.loadList();
					}
				}
			},
			pageSizeChange: function (val) {
				let _this = this;
				if (_this.tab == 'in') {//出库申请切换页码大小
					if (_this.inTab.searchParams.pageSize != val) {
						_this.inTab.searchParams.pageSize = val;
						_this.loadList();
					}
				} else if (_this.tab == 'out') {
					if (_this.outTab.searchParams.pageSize != val) {
						_this.outTab.searchParams.pageSize = val;
						_this.loadList();
					}
				} else if (_this.tab == 'history') {//历史记录
					if (_this.historyTab.searchParams.pageSize != val) {
						_this.historyTab.searchParams.pageSize = val;
						_this.loadList();
					}
				}
			},
			toggleTab: function (name) {
				this.refreshFlag=true;
				this.loadList();
			},
			loadList: function () {
				let _this = this;
				_this.$Message.config({
					top: 200,
					duration: 2
				});
				clearTimeout(timer);
				_this.$Message.destroy();
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '数据请求中...',
						duration: 0
					});
				}, 500);
				if (_this.tab == 'in') {//入库审批
					_this.$http.post('/Storage/enterStorage', _this.inTab.searchParams)
						.then(function (response) {
							var res = response.data;
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							if (res.code == 1) {//正常
								_this.$Message.success({
									content: res.msg
								});
								if(res.data.list){
									_this.inTab.totalPage = res.data.count;
									_this.inTab.searchParams.currentPage = res.data.currentPage;
									_this.inTab.searchParams.pageSize = res.data.pageSize;
									_this.inTab.inList = res.data.list;
								}else {
									_this.inTab.inList = [];
								}
							} else {
								_this.$Message.error({
									content: res.msg
								});
							}
						})
						.catch(function (error) {
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							_this.$Message.error({
								content: error
							});
						});
				} else if (_this.tab == 'out') {//出库审批
					this.$http.post('/Storage/comeStorage', _this.outTab.searchParams)
						.then(function (response) {
							let res = response.data;
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							if (res.code == 1) {//正常
								_this.$Message.success({
									content: res.msg
								});
								if(res.data.list){
									_this.outTab.totalPage = res.data.count;
									_this.outTab.searchParams.currentPage = res.data.currentPage;
									_this.outTab.searchParams.pageSize = res.data.pageSize;
									_this.outTab.outList = res.data.list;
								}else {
									_this.outTab.outList = [];
								}
							} else {
								_this.$Message.error(res.msg);
							}
						})
						.catch(function (error) {
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							_this.$Message.error({
								content: error
							})
						});
				} else if (_this.tab == 'history') {//审批记录
					this.$http.post('/Storage/outStorage', _this.historyTab.searchParams)
						.then(function (response) {
							let res = response.data;
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							if (res.code == 1) {//正常
								_this.$Message.success({
									content: res.msg
								});
								if(res.data.list){
									_this.historyTab.totalPage = res.data.count;
									_this.historyTab.searchParams.currentPage = res.data.currentPage;
									_this.historyTab.searchParams.pageSize = res.data.pageSize;
									_this.historyTab.historyList = res.data.list;
								}else {
									_this.historyTab.historyList = [];
								}
							} else {
								_this.$Message.error(res.msg);
							}
						})
						.catch(function (error) {
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							_this.$Message.error({
								content: error
							})
						});
				}
			},
			dateChange: function (range) {
				let _this = this;
				if (_.includes(range, undefined)) {
					_this.historyTab.searchParams.startTime = '';
					_this.historyTab.searchParams.endTime = '';
				} else {
					_this.historyTab.searchParams.startTime = range[0];
					_this.historyTab.searchParams.endTime = range[1];
				}
			},
			search: function () {
				let _this = this;
				//清空页码
				_this.historyTab.searchParams.currentPage = 1;
				_this.historyTab.searchParams.pageSize = 10;
				//搜索
				this.refreshFlag=true;
				_this.loadList();
			},
			refresh: function () {
				this.refreshFlag=true;
				this.loadList();
			},
			clearAll: function () {//清空搜索，翻页等信息
				this.historyTab.dateRange = [];
				this.historyTab.searchParams.aBaseName = '';
				this.historyTab.searchParams.loanId = '';
				this.historyTab.searchParams.startTime = '';
				this.historyTab.searchParams.endTime = '';
				this.historyTab.searchParams.currentPage = 1;
				this.historyTab.searchParams.pageSize = 10;
			}
		}
	}
</script>
